<template>
  <div id="app" class="d-flex justify-content-center align-items-center">
    <div class="container d-md-flex justify-content-center align-items-center">
      <div class="calculator-container">
        <h1>Calculadora<br> Freelancer</h1>
        <div class="calculo-single">
          <h5>Quanto irá cobrar por hora?</h5>
        <input type="text" v-model="valorHora"/>

        </div><!--calculo-single-->
        
        
        <div class="calculo-single">
        <h5>Quantas horas por dia pretende trabalhar?</h5>
        <input type="text" v-model="horaDia" @keyup="precoDia"/>
        </div><!--calculo-single-->

        <div class="calculo-single">
        <h5>Quantos dias o projeto vai durar?</h5>
        <input type="text" v-model="dias" @keyup="resultado"/>
        </div><!--calculo-single-->
        <h5 class="custom">Quanto sua plataforma cobra em cima de cada projeto?</h5>
        <div class="porcentagem d-flex justify-content-around">
          
          <div class="percents-single">
              <input class="form-check-input" name="num[]" type="radio" value="5" id="5">
              <label class="form-check-label align-middle" id="selecionado4" for="5" @click="percent5">5%</label> 
            </div><!--percents-single-->

          <div class="percents-single">
              <input class="form-check-input" name="num[]" type="radio" value="10" id="10">
              <label class="form-check-label align-middle" id="selecionado4" for="10" @click="percent10">10%</label> 
            </div><!--percents-single-->

          <div class="percents-single">
              <input class="form-check-input" name="num[]" type="radio" value="20" id="20">
              <label class="form-check-label align-middle" id="selecionado4" for="20" @click="percent20">20%</label> 
            </div><!--percents-single-->

          <div class="percents-single">
              <input class="custom" type="text" v-model="percent" placeholder="custom" @keyup="porcentagem"/>
            </div><!--percents-single-->      
        </div><!--porcentagem-->

        
      </div><!--calculator-container-->
      <div class="resultado d-flex flex-column justify-content-center align-items-center">
            <h2>O valor final do projeto será:</h2>
            <h1>R$ {{Math.round(valorTotal)}}</h1>
        </div><!--resultado-->
      </div><!--container-->

  </div>
</template>

<script>

import { ref } from 'vue';
const valorHora = ref("");
const horaDia = ref("");
const valorDia = ref("");
const dias = ref("");
const percent = ref("");
const percentTotal = ref("");
const valorTotal = ref("");
const checked5 = 5/100;
const checked10 = 10/100;
const checked20 = 20/100;
export default {
  name: 'App',
  setup(){
    const precoDia = ()=>{
     valorDia.value = valorHora.value*horaDia.value;
    }


    const porcentagem = ()=>{
          percentTotal.value = percent.value/100*valorTotal.value;
    }
    
    const resultado = ()=>{
     valorTotal.value = valorDia.value*dias.value+percentTotal.value;
    }

     const percent5 = ()=>{
      percentTotal.value = checked5*valorTotal.value;
    }

       const percent10 = ()=>{
      percentTotal.value = checked10*valorTotal.value;
    }

     const percent20 = ()=>{
      percentTotal.value = checked20*valorTotal.value;
    }

   

   

    

    return{ 
      ref,
      valorHora,
      valorDia,
      horaDia,
      dias,
      valorTotal,
      resultado,
      precoDia,
      percent,
      porcentagem,
      percentTotal,
      percent20,
      percent5,
      percent10
    }
  }
}
</script>

